/**
 * @file
 * Seven styles for Tables.
 */
table {
  width: 100%;
  margin: 0 0 10px;
}

caption {
  text-align: left;
  /* LTR */
}

[dir="rtl"] caption {
  text-align: right;
}

th {
  text-align: left;
  /* LTR */
  padding: 10px 12px;
}

[dir="rtl"] th {
  text-align: right;
}

thead th {
  background: #f5f5f2;
  border: solid #bfbfba;
  border-width: 1px 0;
  color: #333;
  text-transform: uppercase;
}

tr {
  border-bottom: 1px solid #e6e4df;
  padding: 0.1em 0.6em;
}

thead > tr {
  border-bottom: 1px solid #000;
}

tbody tr:hover,
tbody tr:focus {
  background: #f7fcff;
}

/* See colors.css */
tbody tr.color-warning:hover,
tbody tr.color-warning:focus {
  background: #fdf8ed;
}

tbody tr.color-error:hover,
tbody tr.color-error:focus {
  background: #fcf4f2;
}

td,
th {
  vertical-align: middle;
}

td {
  padding: 10px 12px;
  text-align: left;
  /* LTR */
}

[dir="rtl"] td {
  text-align: right;
}

th > a {
  position: relative;
  display: flex;
}

/* 1. Must match negative bottom padding of the parent <th> */
th > a:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  bottom: -10px;
  /* 1. */
  left: 0;
  right: 0;
  border-bottom: 2px solid transparent;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

th.is-active > a {
  color: #004875;
}

th.is-active img {
  position: absolute;
  right: 0;
  /* LTR */
  top: 50%;
}

[dir="rtl"] th.is-active img {
  right: auto;
  left: 0;
}

th.is-active > a:after {
  border-bottom-color: #004875;
}

th > a:hover,
th > a:focus,
th.is-active > a:hover,
th.is-active > a:focus {
  color: #008ee6;
  text-decoration: none;
}

th > a:hover:after,
th > a:focus:after,
th.is-active > a:hover:after,
th.is-active > a:focus:after {
  border-bottom-color: #008ee6;
}

td .item-list ul {
  margin: 0;
}

/* This is required to win over specificity of [dir="rtl"] .item-list ul */
[dir="rtl"] td .item-list ul {
  margin: 0;
}

td.is-active {
  background: none;
}

/* Force browsers to calculate the width of a 'select all' <th> element. */
th.select-all {
  width: 1px;
}

/**
 * Captions.
 */
.caption {
  margin-bottom: 1.2em;
}

/**
 * Responsive tables.
 */
@media screen and (max-width: 37.5em) {
  /* 600px */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}

@media screen and (max-width: 60em) {
  /* 920px */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}

/**
 * Tabs.
 */
.is-collapse-enabled .tabs,
.is-horizontal .tabs {
  position: relative;
}

/* Span the full width of the viewport */
.content-header .is-horizontal .tabs:before,
.content-header .is-collapse-enabled .tabs:before {
  left: -2.5em;
  right: -2.5em;
}

/**
 * Tab
 *
 * 1. Required by some elements such as <button>
 * 2. Fixed height needed to ensure alignment with absolutely-positioned
 *    active tab.
 */
.tabs__tab {
  position: relative;
  display: block;
  overflow: hidden;
  box-sizing: border-box;
  width: 100%;
  /* 1 */
  text-overflow: ellipsis;
  white-space: nowrap;
}

[dir="rtl"] .tabs__tab {
  padding-left: 2em;
  padding-right: 1em;
}

li.tabs__tab {
  display: block;
  padding: 0;
}

/* This is required to win over specifity of [dir="rtl"] .tabs__tab */
[dir="rtl"] li.tabs__tab {
  padding-left: 0;
  padding-right: 0;
}

li.tabs__tab a {
  padding: 9px 2em 7px 1em;
  /* LTR */
}

[dir="rtl"] li.tabs__tab a {
  padding-left: 2em;
  padding-right: 1em;
}

.tabs a:hover,
.tabs a:focus {
  text-decoration: none;
}

/* Primary tabs */
.tabs.primary {
  clear: both;
  margin: 0;
}

.tabs.primary .tabs__tab.is-active {
  z-index: 15;
  background-color: #ffffff;
  color: #004f80;
}

.tabs.primary a {
  background: #0678BE;
}

.tabs.primary a:focus {
  color: #008ee6;
}

.tabs.primary .is-active a:focus {
  background: none;
}

/* Only add the arrow if there's space */
.tabs__trigger {
  display: none;
}

/* JS dependent styling */
.is-collapse-enabled .tabs__trigger {
  box-sizing: content-box;
  display: block;
  position: absolute;
  z-index: 10;
  right: 0;
  /* LTR */
  top: 2px;
  left: auto;
  /* LTR */
  width: 25%;
  padding-right: 4px;
  padding-left: 4px;
  font-family: Arial, sans-serif;
  font-size: 1.25em;
  letter-spacing: 0.1em;
  text-align: center;
  outline: 0;
}

[dir="rtl"] .is-collapse-enabled .tabs__trigger {
  right: auto;
  left: 0;
}

.is-collapse-enabled .tabs {
  padding-top: 38px;
  max-height: 0;
}

.tabs.is-open {
  max-height: 999em;
  padding-bottom: 16px;
  padding-bottom: 1rem;
}

.is-collapse-enabled .tabs__tab.is-active {
  position: absolute;
  top: 2px;
  left: 0;
  /* LTR */
  width: 75%;
  border-bottom: 0;
}

[dir="rtl"] .is-collapse-enabled .tabs__tab.is-active {
  left: auto;
  right: 0;
}

.is-collapse-enabled .tabs.primary a.is-active:before {
  content: none;
}

.is-open .tabs__tab.is-active {
  background-color: #fff;
  color: #0678BE;
}

/* Styles for the horizontal state always take priority */
.is-horizontal .tabs {
  max-height: none !important;
  padding-top: 0 !important;
  overflow: visible;
}

.is-horizontal .tabs__tab {
  float: left;
  /* LTR */
  height: auto;
  width: auto;
  margin: 3px;
  border-radius: 3px;
  text-align: center;
}

[dir="rtl"] .is-horizontal .tabs__tab {
  float: right;
  /* This is required to win over specifity of [dir="rtl"] .tabs > li */
}

/* Override the states above */
.is-horizontal .tabs__tab.is-active,
.is-horizontal .tabs.primary .tabs__tab.is-active,
[dir="rtl"] .is-horizontal .tabs.primary .tabs__tab.is-active {
  position: relative;
  width: auto;
  top: 0;
}

.is-horizontal .tabs.primary a {
  background-image: none;
  padding: .3em .6em;
}

.is-horizontal .tabs__trigger {
  display: none;
}

/* Secondary tabs */
.tabs.secondary {
  display: block;
  margin: 0;
}

.tabs.secondary .tabs__tab {
  display: block;
  -webkit-transition: border-color 0.2s, background-color 0.2s;
  transition: border-color 0.2s, background-color 0.2s;
}

/* This is required to win over specificity of [dir="rtl"] .tabs.secondary .tabs__tab */
[dir="rtl"] .views-displays .tabs.secondary li,
[dir="rtl"] .views-displays .tabs.secondary li.is-active {
  padding-left: 0;
  padding-right: 0;
}

.tabs.secondary .tabs__tab + .tabs__tab {
  border-top: 1px solid #d9d8d4;
}

.tabs.secondary .tabs__tab.is-active {
  color: #004f80;
  border-left: 2px solid #004f80;
  /* LTR */
  padding-left: 15px;
  /* LTR */
}

[dir="rtl"] .tabs.secondary .tabs__tab.is-active {
  border-left: 1px solid #bfbfbf;
  border-right: 2px solid #004f80;
  padding-right: 15px;
}

/* This is required to win over specificity of [dir="rtl"] .tabs.secondary .tabs__tab.is-active */
[dir="rtl"] .views-displays .tabs.secondary li.is-active {
  border: 0 none;
  padding-right: 0;
}

.tabs.secondary .tabs__tab:hover,
.tabs.secondary .tabs__tab:focus {
  color: #008ee6;
  border-left: 2px solid #008ee6;
  /* LTR */
  padding-left: 15px;
  /* LTR */
}

[dir="rtl"] .tabs.secondary .tabs__tab:hover,
[dir="rtl"] .tabs.secondary .tabs__tab:focus {
  border-left: 1px solid #bfbfbf;
  border-right: 2px solid #008ee6;
  padding-right: 15px;
}

/* This is required to win over specificity of [dir="rtl"] .tabs.secondary .tabs__tab:hover */
[dir="rtl"] .views-displays .tabs li.tabs__tab:hover {
  border: 0 none;
  padding-right: 0;
}

.tabs.secondary a {
  background-color: transparent;
  padding: 7px 13px 5px;
  text-decoration: none;
}

/* This is required to win over specificity of [dir="rtl"] li.tabs__tab a */
[dir="rtl"] .tabs.secondary a {
  padding-left: 13px;
  padding-right: 13px;
}

/* This is required to win over specificity of [dir="rtl"] .tabs.secondary a */
[dir="rtl"] .views-displays .tabs.secondary a {
  padding-left: 7px;
  padding-right: 7px;
}

.tabs.secondary .is-active a {
  color: #004f80;
}

.tabs.secondary a:focus {
  text-decoration: underline;
}

/* Styles for the horizontal state */
.is-horizontal .tabs.secondary .tabs__tab a {
  text-decoration: none;
}

.is-horizontal .tabs.secondary .tabs__tab {
  background: none;
  float: left;
  /* LTR */
  position: relative;
  top: 0;
  z-index: 15;
  border-bottom: 2px solid transparent;
  border-left: 1px solid transparent;
  /* LTR */
  border-right-color: transparent;
  /* LTR */
  border-top: 0;
  padding: 0;
}

/**
 * 1. This is required to win over specifity of
 *      [dir="rtl"] .tabs.secondary .tabs__tab:hover,
 *      [dir="rtl"] .tabs.secondary .tabs__tab:focus
 */
[dir="rtl"] .is-horizontal .tabs.secondary .tabs__tab {
  float: right;
  border-right: 1px solid transparent;
  border-left-color: transparent;
  padding-right: 0;
  /* 1 */
}

.is-horizontal .tabs.secondary .tabs__tab.is-active {
  border-bottom-color: #004f80;
}

.is-horizontal .tabs.secondary .tabs__tab:hover,
.is-horizontal .tabs.secondary .tabs__tab:focus {
  border-bottom-color: #008ee6;
}

.logo,
.site-branding__logo {
  display: block;
  z-index: 501;
