  transition: transform .2s, all .2s ease;
  max-width: 120px;
  box-sizing: border-box;
}

.logo::after,
.site-branding__logo::after {
  will-change: transform, width;
  background: #0678BE;
  content: " ";
  position: absolute;
  display: block;
  z-index: -1;
  top: 5px;
  left: 5px;
  width: 100px;
  height: 100px;
}

.logo.not-fixed img,
.site-branding__logo.not-fixed img {
  transition: transform .2s, all .2s ease .2s;
}

.logo.not-fixed:after,
.site-branding__logo.not-fixed:after {
