}

.logo.fixed img,
.site-branding__logo.fixed img {
  margin: 5px 0;
  padding: 0 40px 20px 0;
  max-height: 75px;
}

.logo.fixed:after,
.site-branding__logo.fixed:after {
  transition: transform .2s, width .2s ease .2s;
  transform: rotate(0) skew(0) translate3d(-5px, -40px, 0);
